<template>
  <b-row>
      <b-col lg="6">
      
        <c-table :table-data="items" :fields="fields" caption="Product Details"></c-table>
      
    </b-col>
      <b-col lg="6">

        <b-card>
          <div slot="header">
            <strong>Update/Delete</strong> User
          </div>
          <b-form v-on:submit.prevent="updateData">
            <b-form-group
              description="Enter the Name"
              label="Name"
              label-for="basicName"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="basicName" v-model="posts.name" type="text" autocomplete="name"></b-form-input>
            </b-form-group>
            <b-form-group
              label="Date of Birth" label-for="date"
              :label-cols="3"
              :horizontal="true">
              <b-form-input type="date" v-model="posts.dob" id="date"></b-form-input>
            </b-form-group>
            <b-form-group
              description="Email Id"
              label="Email"
              label-for="basicText"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="basicText" v-model="posts.email" type="email"></b-form-input>
            </b-form-group>
            <b-form-group
              description="Phone Number"
              label="Phone No."
              label-for="basicText"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="basicText" v-model="posts.phone" type="text"></b-form-input>
            </b-form-group>
            <b-form-group
              description="Link to the Profile Picture"
              label="Profile Pic"
              label-for="basicText"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="basicText" v-model="posts.profile_pic" type="text"></b-form-input>
            </b-form-group>
            <b-form-group
              label="User Category"
              label-for="basicSelect"
              :label-cols="3"
              :horizontal="true">
              <b-form-select id="basicSelect"
                :plain="true"
                :options="['Please select','Super Admin','Client','Seller','Staff','Manufacturer']"
                value="Please select" v-model="posts.category" >
              </b-form-select>
            </b-form-group>
            <b-form-group
              description="Link to the Profile Picture"
              label="Address"
              label-for="basicText"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="basicText" v-model="posts.address" type="text"></b-form-input>
            </b-form-group>

          <div slot="footer">
            <b-button type="submit" size="sm" variant="primary"><i class="fa fa-dot-circle-o"></i> Submit</b-button>
            <b-button v-on:click="delData()" type="submit" size="sm" variant="danger"><i class="fa fa-ban"></i> Delete</b-button>
          </div>
          </b-form>
        </b-card>
        </b-col>
    </b-row>
</template>

<script>
  import cTable from './Table.vue'
  import axios from 'axios'
  

  var link = window.location.href
  var key = link.split("users/",24).pop()
  console.log(key)

export default {
  name: 'dashboard',
  components: {
    cTable
  },
  data: function () {
    return {
      items:[],
      itemsArray: [],
      fields: [
        {key: 'name', label: 'Username', sortable: true},
        {key: 'phone',label: 'Phone Number'},
        {key: 'dob',label: 'Date Of Birth'},
        {key: 'category',label: 'Category Of User'}, 
        

      ],
      selected: 'Month',
      // posts: [],
      errors: [],
      posts: {
                name: "",
                id: "",
                dob: "",
                email: "",
                profile_pic: "",
                phone: "",
                category:"",
                address:"",
                _id: key
                },
                response: "",
      }
  },
  async mounted() {
    axios.get(`https://selacious-cloud-siteapi.herokuapp.com/users/${key}`)
    .then(response => {this.posts = response.data})
    axios.get(`https://selacious-cloud-siteapi.herokuapp.com/users/${key}`)
    .then(
      response => {
      this.itemsArray = response.data
      this.items = [this.itemsArray]
    });
  },
  methods: {
    variant (value) {
      let $variant
      if (value <= 25) {
        $variant = 'info'
      } else if (value > 25 && value <= 50) {
        $variant = 'success'
      } else if (value > 50 && value <= 75) {
        $variant = 'warning'
      } else if (value > 75 && value <= 100) {
        $variant = 'danger'
      }
      return $variant
    },
    flag (value) {
      return 'flag-icon flag-icon-' + value
    },
    async updateData() {
      axios.put(`https://selacious-cloud-siteapi.herokuapp.com/users/${key}`,this.posts).then((response) =>{
        console.log(response);
        this.$router.push("/users");
      });
    },
    async delData() {
                axios({ method: "DELETE", "url": `https://selacious-cloud-siteapi.herokuapp.com/users/${key}`, "data": this.posts, "headers": { 'Content-Type': 'application/x-www-form-urlencoded' } }).then(result => {
                    this.posts = result.data;
                    this.$router.push("/users");
                }, error => {
                    console.error(error);
                });
    }
  }
}

</script>

<style>
  /* IE fix */
  #card-chart-01, #card-chart-02 {
    width: 100% !important;
  }
</style>
